.keywordMonitorDetail{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    background: #f2f2f9;
    min-width: 1100px !important;
    .Breadcrumb{
        background: #fff;
        padding: 10px 20px;
        margin-bottom: 12px;
    }
    .content{
        display: flex;
        margin: 0px 20px;
        flex-grow: 1;
        flex-direction: column;
        overflow: auto;
        padding: 8px 0px;
        .contentTop{
            padding: 20px 10px;
            background: #fff;
            margin-bottom: 10px;
            color: #333;
            .div{
                margin-top: 10px;
                margin-bottom: 10px;
                display: flex;
                font-size: 14px;
                span{
                    flex: 1;
                    font-size: 14px;
                }
            }
            .footer{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 20px;
                flex-shrink: 0;
                .info{
                    font-size: 14px;
                    color: #999;
                    width: auto;
                    flex: 1;
                }
                .ant-pagination {
                    flex: 2;
                    text-align: right;
                }
            }
        }
        
    }
    .copyright{
        text-align: center;
        padding: 10px;
        font-size: 12px;
    }
}